.body {
    background: #29656b;
    border-radius: 5px;
  }
  .st{
    padding: 2px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .sttext{
    color: antiquewhite;
    border-radius: 4px;
    border: 1px solid white;
    padding: 2px;
    margin-top: 10px;
    margin-bottom: 10px;
  }
  .resetStatus{
    background: #901313;
    color: white;
    border: none;
    padding: 20px;
    font-weight: 10;
    letter-spacing: 3px;
    /* width: 10%; */
    display: flex;
  }

  .form {
    background: #193f43;
    max-width: 600px;
    margin: 0 auto;
  }
  
  .h1 {
    font-weight: 100;
    color: white;
    text-align: center;
    padding-bottom: 10px;
    border-bottom: 1px solid rgb(79, 98, 148);
  }
  
  
  
  .input {
    display: block;
    box-sizing: border-box;
    width: 90%;
    border-radius: 4px;
    border: 1px solid white;
    padding: 10px 15px;
    margin-bottom: 10px;
    margin-left: 10px;
  }
  
  .label {
    line-height: 2;
    text-align: left;
    display: block;
    margin-bottom: 13px;
    margin-top: 23px;
    color: white;
    font-weight: 200;
    margin-left: 5px;
  }
  
  .button[type="submit"],
  .input[type="submit"] {
    background: #901313;
    color: white;
    border: none;
    margin-top: 10px;
    padding: 20px;
    font-weight: 100;
    letter-spacing: 10px;
    width: 100%;
  }
  .status{
/* margin-left: 20px; */
  }

  .button[type="submit"]:hover,
  .input[type="submit"]:hover {
    background: #27941f;
  }
  
  .button[type="submit"]:active,
  .input[type="button"]:active,
  .input[type="submit"]:active {
    transition: 0.3s all;
    transform: translateY(3px);
    border: 1px solid transparent;
    opacity: 0.8;
  }
  
  .input:disabled {
    opacity: 0.4;
  }
  
  .input[type="button"]:hover {
    transition: 0.3s all;
  }
  
  .button[type="submit"],
  .input[type="button"],
  .input[type="submit"] {
    -webkit-appearance: none;
  }
  
  .Login {
    max-width: 600px;
    margin: 0 auto;
  }
  
  .button[type="button"] {
    display: block;
    appearance: none;
    background: #333;
    color: white;
    border: none;
    text-transform: uppercase;
    padding: 10px 20px;
    border-radius: 4px;
  }
  
  .hr {
    margin-top: 30px;
  }
  
  .i {
    color: white;
  }
  
  .span {
    color: tomato;
  }
  