
  .loader-container {
    display: flex;
    justify-content: left;
    align-items: left;
    height: 100%;
  }
  
  .loader {
    border: 16px solid #f3f3f3;
    border-top: 16px solid #064119;
    border-radius: 50%;
    width: 120px;
    height: 120px;
    animation: spin 2s linear infinite;
    transition: all 0.3s ease;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  